
export default function Topper(props) {

    const topperContainerStyle = {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '2px', // Adjust the padding as needed
        fontSize: '14px',
        borderBottom: '1px solid #8c8c8c',  // Border style and color
        backgroundImage: 'linear-gradient(to bottom, #f0f0f0, #D3D3D3)', // light gray to Dark gray gradient
        color: 'black', // Text color, adjust as needed
    };

    const countStyle = {
        margin: '0 10px', // Adjust the margin as needed
    };

    return (
        <div style={topperContainerStyle}>
            <div style={countStyle}>
                Total Views: <span>1000</span> {/* Replace with actual total views count */}
            </div>
            <div style={countStyle}>
                Contacted People: <span>500</span> {/* Replace with actual contacted people count */}
            </div>
        </div>
    );
}